<template>
    <views class="search">
        <views class="search-left">
            <views class="search-content">
                <views class="search-icon">
                    <images :src="searchIcon"></images>
                </views>
                <views class="search-input">
                    <!-- form ios手机上唤起搜索按钮（默认是换行按钮）  -->
                    <form action="javascript:return true">
                        <input
                            v-focus
                            autofocus
                            @keypress="searchBtn"
                            type="search"
                            placeholder="请输入搜索标题"
                            v-model="keyword"
                        />
                    </form>
                </views>
            </views>
        </views>
        <views class="search-right" @click="handleCancel">取消</views>
    </views>
</template>

<script>
import tools from '@/mixins/tools'
export default {
    mixins: [tools],
    data() {
        return {
            keyword: '',
            searchIcon: require('@/assets/images/sy_ss@3x.png')
        }
    },
    computed: {
        querys() {
            return this.$route.query
        }
    },
    watch: {
        'querys.keyword': {
            handler(val) {
                if (val) this.keyword = val
            },
            immediate: true
        }
    },
    methods: {
        searchBtn(event) {
            if (event.keyCode === 13) { // 如果按的是enter键 13是enter
                event.preventDefault() // 禁止默认事件（默认是换行）
                var searchVal = event.target.value
                const { to, categoryId, categoryName } = this.querys
                let url = ''
                if (to === 'faq') {
                    url = `/${to}?keyword=${searchVal}`
                } else {
                    url = `/${to}?categoryName=${categoryName}&categoryId=${categoryId}&keyword=${searchVal}`
                }
                this.replace(url)
            }
        },
        handleCancel() {
            this.back()
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
